<template>
  <div class="page-container">
    <v-breadcrumb />
    <div class="page-content" v-loading="loading">
      <div class="mb-20">
        <el-tabs v-model="activeName">
          <el-tab-pane label="" name="1">
            <h3></h3>
            <el-table :data="sms_logData" style="width: 100%">
              <el-table-column prop="name" label=""> </el-table-column>
              <el-table-column prop="phone" label=""> </el-table-column>
              <el-table-column prop="content" label=""> </el-table-column>
              <el-table-column prop="type" label=""> </el-table-column>
              <el-table-column prop="state" label="">
                <template slot-scope="scope">
                  <el-tag v-if="scope.row.state == 0" type="success"
                    ></el-tag
                  >
                  <el-tag v-if="scope.row.state == 1" type="info"
                    ></el-tag
                  >
                </template>
              </el-table-column>
            </el-table>
            <div>
              <el-pagination
                v-if="sms_logPagination.total > sms_logPagination.pageSize"
                background
                layout="prev, pager, next"
                :current-page.sync="sms_logPagination.currentPage"
                :page-size="sms_logPagination.pageSize"
                :total="sms_logPagination.total"
                @current-change="getSms_logDate"
              >
              </el-pagination>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <h3></h3>

            <el-row :gutter="20" style="margin-bottom:30px;">
              <el-col :lg="6"
                ><el-card class="box-card">
                  <div slot="header" class="clearfix">
                    <span></span>
                    <el-button style="float: right; padding: 3px 0" type="text">
                      <el-radio v-model="radio" label="1"
                        ></el-radio
                      ></el-button
                    >
                  </div>
                  13400000000
                  <h5></h5>
                  <el-form
                    :model="leaveForm"
                    label-position="left"
                    status-icon
                    :rules="rules"
                    ref="leaveForm"
                    label-width="100px"
                    style="margin-top:10px; max-width:320px;"
                    class="demo-ruleForm"
                  >
                    <el-form-item label="" prop="date">
                      <el-date-picker
                        v-model="leaveForm.date"
                        type="date"
                        placeholder=""
                      >
                      </el-date-picker>
                    </el-form-item>
                    <el-form-item label="" prop="className">
                      <el-input
                        v-model="leaveForm.className"
                        placeholder=""
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="time">
                      <el-time-picker
                        v-model="leaveForm.time"
                        placeholder=""
                      >
                      </el-time-picker>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                      <el-input v-model.number="leaveForm.phone"></el-input>
                    </el-form-item>
                  </el-form> </el-card
              ></el-col>
              <el-col :lg="6"><el-card class="box-card"></el-card></el-col>
              <el-col :lg="6"><el-card class="box-card"></el-card></el-col>
              <el-col :lg="6"><el-card class="box-card"></el-card></el-col>
            </el-row>
            <div>
              <el-form label-position="right" inline label-width="80px">
                <el-form-item label="">
                  <el-input v-model="formData.name" placeholder="" />
                </el-form-item>
                <el-form-item label="">
                  <el-input
                    v-model="formData.phone"
                    placeholder=""
                  />
                </el-form-item>
                <el-form-item style="margin-left:30px;">
                  <el-button type="primary" @click="getUserData"
                    ></el-button
                  >
                  <el-button @click="handleReset"></el-button>
                </el-form-item>
              </el-form>
            </div>

            <el-table
              :data="
                userData.filter(
                  data =>
                    !search ||
                    data.name.toLowerCase().includes(search.toLowerCase())
                )
              "
              ref="multipleTable"
              tooltip-effect="dark"
              style="width: 100%"
              @selection-change="handleSelectionChange"
            >
              <el-table-column type="selection" width="55"> </el-table-column>
              <el-table-column prop="name" label=""> </el-table-column>
              <el-table-column prop="phone" label=""> </el-table-column>
              <el-table-column prop="sex" label="">
                <template slot-scope="scope">
                  <el-tag :type="scope.row.sex === 1 ? '' : 'success'">
                    {{ scope.row.sex === 1 ? "" : "" }}</el-tag
                  >
                </template>
              </el-table-column>
              <el-table-column prop="birthday" label="" />
              <el-table-column prop="sms_name" label="" />
              <el-table-column prop="sms_phone" label="" />
              <el-table-column prop="balance" label="/" />
              <el-table-column align="right">
                <template slot="header">
                  <el-input
                    v-model="search"
                    size="mini"
                    placeholder=""
                  />
                </template>
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    type="danger"
                    @click="sendSms_log(scope.$index, scope.row)"
                    ></el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <div>
              <el-pagination
                v-if="pagination.total > pagination.pageSize"
                background
                layout="prev, pager, next"
                :current-page.sync="pagination.currentPage"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="getUserData"
              >
              </el-pagination>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="3"></el-tab-pane>
          <el-tab-pane label="" name="4"></el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
import Breadcrumb from "@/components/BasicBreadcrumb.vue";
import userService from "@/global/service/user.js";
import sms_logService from "@/global/service/sms_log.js";
import dateFunction from "@/utils/authcode.js";
export default {
  data() {
    return {
      loading: false,
      activeName: "2",
      loading: true,
      sms_logData: [],
      userData: [],
      pagination: {
        total: 0,
        currentPage: 1,
        pageSize: 10
      },
      sms_logPagination: {
        total: 0,
        currentPage: 1,
        pageSize: 10
      },
      formData: {
        name: "",
        phone: ""
      },
      multipleSelection: [],
      search: "",
      radio: "",
      leaveForm: {
        date: "",
        time: new Date(2016, 9, 10, 18, 40),
        phone: "",
        className: ""
      },
      rules: {
        date: [{ required: true, message: "", trigger: "blur" }],
        time: [{ required: true, message: "", trigger: "blur" }],
        className: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created() {
    this.getUserData();
    this.getSms_logDate();
  },
  methods: {
    getSms_logDate() {
      let params = {
        current_page: this.sms_logPagination.currentPage,
        page_size: this.sms_logPagination.pageSize,
        name: this.formData.name,
        phone: this.formData.phone
      };
      sms_logService
        .showSms_log(params)
        .then(res => {
          this.sms_logData = res.datas;
          this.sms_logPagination.pageSize = Number(
            res.sms_logPagination.page_size
          );
          this.sms_logPagination.currentPage = Number(
            res.sms_logPagination.current_page
          );
          this.sms_logPagination.total = Number(res.sms_logPagination.total);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getUserData() {
      let params = {
        current_page: this.pagination.currentPage,
        page_size: this.pagination.pageSize,
        name: this.formData.name,
        phone: this.formData.phone
      };

      userService
        .list(params)
        .then(res => {
          this.userData = res.datas;
          this.pagination.pageSize = Number(res.pagination.page_size);
          this.pagination.currentPage = Number(res.pagination.current_page);
          this.pagination.total = Number(res.pagination.total);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    handleReset() {
      this.formData.name = "";
      this.formData.phone = "";
      this.pagination.currentPage = 1;
      this.getUserData();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    sendSms_log: function(index, row) {
      let indexs = this.radio;
      switch (Number(indexs)) {
        case 1:
          this.sendSms_logLeave(row);
          break;
        case 2:
          break;
        default:
          this.$message.error("");
      }
    },
    sendSms_logLeave: function(row) {
      this.$refs.leaveForm.validate(valid => {
        if (valid) {
          let leaveForm = this.leaveForm;
          let time =
            new Date(leaveForm.time).getHours() +
            ":" +
            new Date(leaveForm.time).getMinutes();
          let date = dateFunction.formatDate(leaveForm.date);
          let params = {
            sms_logJson: {
              name: row.name,
              className: leaveForm.className,
              date: date,
              time: time,
              phone: leaveForm.phone
            },
            userPhone: row.phone,
            user_id: row.id,
            template_code: "SMS_173760644",
            content: `${row.name}${leaveForm.className}${date}-${time}${leaveForm.phone}`,
            type: ""
          };
          sms_logService.sendSms_log(params).then(res => {
            this.$message({
              message: "",
              type: "success"
            });
          });
        } else {
          this.$message.error("");
        }
      });
    }
  },
  components: {
    "v-breadcrumb": Breadcrumb
  }
};
</script>

<style type="text/css" lang="less" scoped></style>
